@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap);
* {
	box-sizing: border-box;
}

body {
	background-color: #dddddd;
	font-family: 'Poppins', sans-serif;
	margin: 0;
}

header {
	background-color: #868686;
	display: flex;
	justify-content: flex-end;
	padding: 1rem;
}

.search {
	background-color: transparent;
	border: 2px solid #c2c2c2;
	border-radius: 50px;
	color: rgb(44, 44, 44);
	font-family: inherit;
	font-size: 1rem;
	padding: 0.5rem 1rem;
}

.search::-webkit-input-placeholder {
	color: #eeeeee;
}

.search::placeholder {
	color: #eeeeee;
}

.search:focus {
	background-color: #afafaf;
	outline: none;
}

main {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

/* Scroll Bar */

/* width */
::-webkit-scrollbar {
	width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
	box-shadow: inset 0 0 5px grey;
	border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: rgba(182, 23, 23, 0.4);
	border-radius: 10px;
}

a {
	color: black;
	text-decoration: none;
}
img {
	width: 100%;
	display: block;
	object-fit: stretch;
}
.Card_card__9TGoO {
	width: 300px;
	padding: 1rem;
	border-radius: 20px;
	color: #333;
	background: #fcfcfc;
	box-shadow: 0 8px 8px rgba(0, 0, 0, 0.3);
	margin: 1.2rem;
	position: relative;
}
p {
	padding: 8px 0;
}
h3 {
	padding: 10px 0;
	line-height: 1.4;
}
a:hover {
	text-decoration: underline;
}
.Card_comments__1mLlE {
	font-weight: 400;
	bottom: 10px;
}

